.contact {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 1em;
    margin-top: 4em;
    margin-bottom: 4em;
}

.contact a {
    text-decoration: underline;
    color: inherit;
    font-weight: bold;
}

.contact img {
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4em;
}

.contact h3 {
    color: white;
    font-size: 2em;
}

.contact iframe {
    display: inline-block;
    margin: 0 auto;
    margin-top: 2em;
    margin-bottom: 2em;
    width: 45vw;
    height: 55vh;
}

@media screen and (max-width: 400px) {
    .contact iframe {
        width: 60vw;
        height: 50vh;
    }
  }
